// PAGINATION
.paginationk {
	display: flex;
	flex-wrap: wrap;
	margin-top: 20px;

	&.center {
		justify-content: center;

		.paginationk-list {
			justify-content: center; } }

	&.right {
		justify-content: flex-end;

		.paginationk-list {
			justify-content: flex-end; } }

	&-list {
		display: flex;
		flex-wrap: wrap;

		.item {
			margin-left: 10px;
			margin-bottom: 5px;

			.link {
				display: flex;
				height: 40*$rem;
				width: 40*$rem;
				overflow: hidden;
				justify-content: center;
				align-items: center;
				// border-radius: 50%
				color: #fff;
				// background-color: #E3E3E3
				font-size: 14*$rem;
				line-height: 1;
				// font-family: $fm
				border: 1px solid #fff; }

			&.active,
			&:hover {
				.link {
					color: #fff;
					background-color: $cl_main;
					border-color: $cl_main; } }

			&.disabled {
				// display: none
				opacity: 0.5;

				.link {
					cursor: default;
					user-select: none; }

				&:hover {
					.link {
						background-color: transparent;
						border-color: #fff; } } }

			&:first-child,
			&:last-child {
				.link {
					&:before {
						font-family: "FontAwesome";
						font-size: 12*$rem; } } }

			&:first-child {
				.link {
					&:before {
						content: "\f053"; } } }

			&:last-child {
				.link {
					&:before {
						content: "\f054"; } } }

			// +media(sm, emp, -1)
			//  display: none

			//  &:first-child,
			//  &:last-child
 }			//    display: block

		// +media(xs, emp, -1)
		// 	.item
		// 		.link
		// 			font-size: 14*$rem
		// 			height: 26*$rem
 } }		// 			width: 26*$rem
