// SECTION
.section {
	padding-top: 1.75rem;
	padding-bottom: 1.75rem;
	overflow: hidden;

	&-bg {
		&-gray {
			position: relative;

			&::before {
				content: "";
				background-color: rgba(0,0,0,.6);
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				pointer-events: none; }

			.container {
				position: relative; } } }

	.breadcrumbk {
		padding: 0;
		margin-bottom: 2.5rem;
		background-color: transparent; }

	@include media(sm, emp, 0) {
		padding-top: 3rem;
		padding-bottom: 3rem; } }

// BOOKING
.booking {
	&-form {
		padding: 1rem 1.5rem;
		background-color: $cl_white;
		border-radius: 0.5rem;

		.row {
			margin-left: -0.5rem;
			margin-right: -0.5rem; }

		[class ^= 'col'] {
			padding-left: 0.5rem;
			padding-right: 0.5rem; }

		.input {
			&-icon {
				position: relative;

				.icon {
					@include align(ver);
					left: .5rem;
					pointer-events: none; }

				.form-control {
					padding-left: 2rem;
					height: 2rem;
					font-size: inherit;
					border-color: rgba($cl_60, .5);

					&::placeholder {
						color: rgba($cl_60, .5); } } } }

		&__item {
			.label {
				display: inline-block;
				margin-bottom: 0.25em;
				font-weight: bold; }

			&.divided {
				border-bottom: thin solid $cl_main; } }

		&--horizontal {
			display: flex;
			flex-wrap: wrap;
			align-items: flex-end;
			border: thin solid rgba($cl_60, .5);

			.btn-main {
				flex: 0 0 100%;
				max-width: 100%;
				height: 2rem; }

			.col {
				flex: 0 0 50%;
				margin-bottom: 1em;

				&-large {
					flex: 0 0 100%; } } }

		&--verticle {
			padding-top: 2rem;
			padding-bottom: 2rem;

			.booking {
				&-form {
					&__item {
						margin-bottom: 1rem; } } } }

		@include media(sm, emp, 0) {
			.btn-main {
				flex: 0 0 3rem; }

			&--horizontal {
				flex-wrap: nowrap;

				.btn-main {
					margin-left: 1rem; }

				.row {
					flex-grow: 1; }

				.col {
					flex: 0 0 25%;
					margin-bottom: 0;

					&-large {
						flex: 0 0 100%;
						margin-bottom: 1rem; } } }

			&__item {
				.label {
					margin-bottom: 0.75rem; } } }

		@include media(md, emp, 0) {
			&--horizontal {
				.col {
					&-large {
						flex: 0 0 percentage(416/1120);
						margin-bottom: 0; }

					&-medium {
						flex: 0 0 percentage(208/1120); }

					&-small {
						flex: 0 0 percentage(144/1120); } } } } } }

// ITEM
.item {
	&-card {
		.inner {
			transition: box-shadow 350ms;

			&::before {
				content: "";
				position: absolute;
				bottom: 0;
				left: 0;
				height: 33.333%;
				width: 100%;
				pointer-events: none;
				background: linear-gradient(180deg, rgba($cl_60, 0) 0%, $cl_60 100%);
				z-index: 1; } }

		.img {
			@include img_box(224, 336);

			img {
				transition: transform 650ms; } }

		.caption {
			position: absolute;
			bottom: 0;
			left: 0;
			padding: 1rem;
			color: $cl_white;
			z-index: 1;
			transition: padding 350ms; }

		.title {
			font-size: 1.25rem;
			font-weight: bold;
			margin-bottom: 0; }

		&:hover {
			.inner {
				box-shadow: 0 .25rem 1rem rgba(0, 0, 0, 0.25); }

			.img {
				img {
					transform: translate(-50%, -50%) rotate(4deg) scale(1.1); } }

			.caption {
				padding-bottom: 1.25rem; } }

		@include media(sm, emp, 0) {
			.title {
				font-size: 1.5rem; } } }

	&-container {
		margin-top: 1rem;
		margin-bottom: 1rem;

		.inner {
			&::before {
				content: "";
				position: absolute;
				top: -0.25rem;
				right: -0.25rem;
				bottom: -0.25rem;
				left: -0.25rem;
				transition: box-shadow 350ms;
				pointer-events: none; } }

		.img {
			@include img_box(280, 192);

			img {
				transition: transform 1s; } }

		.type {
			font-size: .75rem;
			text-transform: uppercase; }

		.title {
			font-size: 1.125rem;
			font-weight: bold;
			margin-bottom: 0.5rem;
			transition: color 250ms; }

		.price {
			color: $cl_main;
			font-weight: bold; }

		.content {
			line-height: 1.4; }

		.date-time {
			font-size: .75rem;

			.item {
				display: flex;
				align-items: center;

				&:not(:last-child) {
					margin-bottom: 0.5rem; } }

			.icon {
				margin-right: 0.5rem;
				fill: currentColor; } }

		.author {
			font-size: .75rem;

			&-img {
				height: 2rem;
				width: 2rem;
				border-radius: 50%;
				overflow: hidden;
				margin-right: 0.5rem;

				img {
					@include obfit; } } }

		.comment-rating {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;
			font-size: .75rem;
			margin-bottom: 0.5rem;

			.point {
				height: 2rem;
				width: 2rem;
				border-radius: 0.5rem;
				color: $cl_white;
				background-color: $cl_main;
				font-weight: bold;
				display: flex;
				align-items: center;
				justify-content: center;
				overflow: hidden; }

			.left {
				display: flex;
				align-items: center;
				flex-wrap: wrap; }

			.item {
				margin-bottom: 0.25rem; } }

		.labels {
			position: absolute;
			top: 1rem;
			left: -0.5rem;

			.label {
				padding: .5rem 1rem;
				font-size: .75rem;
				border-radius: 0 .5rem .5rem 0;
				pointer-events: none;

				&:not(:last-child) {
					margin-bottom: 0.5rem; }

				&-main {
					color: $cl_white;
					background-color: $cl_main; } } }

		&:hover {
			.inner {
				&::before {
					box-shadow: 0 .25rem 1rem rgba(0, 0, 0, 0.25); } }

			.title {
				color: $cl_main; }

			.img {
				img {
					transform: translate(-50%, -50%) rotate(4deg) scale(1.1); } }

			.btn-line {
				color: $cl_white;
				background-color: $cl_main;
				box-shadow: 0 0 0 0.5px $cl_main; } }

		&-wrap {
			margin-top: -1rem; }

		&--plane {
			.inner {
				padding: 1rem 1.25rem;
				box-shadow: 0px .25rem 1.25rem rgba(0, 0, 0, 0.1);

				&::before {
					display: none; } }

			.place {
				display: flex;
				flex-wrap: wrap;

				&-wrap {
					display: inline-flex;
					flex-direction: column; }

				.label {
					color: $cl_black; }

				.item {
					&:not(:first-child) {
						&::before {
							content: "";
							height: 1rem;
							width: 1rem;
							display: inline-block;
							vertical-align: middle;
							margin-left: 0.5rem;
							margin-right: 0.5rem;
							background: url(../img/icons/arrow-switch.svg) no-repeat center center / contain; } } } }

			.price {
				font-size: 1.125rem;

				.unit {
					font-size: .875rem;
					font-weight: normal;
					margin-left: 0.5rem; } } }

		@include media(lg, emp, 0) {
			&-wrap {
				&.gutter-lg {
					margin-top: -3rem;

					.item-container {
						margin-top: 3rem; } } } } } }

// COLUMN
.column {
	&-list {
		background-color: rgba($cl_60, .1);
		max-height: 25rem;
		overflow-y: auto;

		.item {
			display: block;
			padding: .5rem 1.5rem; }

		@include media(sm, emp, 0) {
			max-height: none;
			column-count: 2;
			column-gap: normal;
			column-fill: auto;
			column-rule: solid white 1.5rem; }

		@include media(md, emp, 0) {
			column-count: 3; }

		@include media(lg, emp, 0) {
			column-count: 5; } } }

.page-load-more {
	padding-top: 1.5rem;

	@include media(sm, emp, 0) {
		padding-top: 4rem; } }

.plane-partner {
	display: flex;
	flex-wrap: wrap;
	margin: -1rem -0.5rem 0;

	.item {
		margin-top: 1rem;
		padding-left: 0.5rem;
		padding-right: 0.5rem;
		flex: 0 0 percentage(1 / 3);
		max-width: percentage(1 / 3); }

	.img {
		box-shadow: 0 0 0 .5px rgba($cl_60, .5);

		&::before {
			content: "";
			padding-top: percentage(80/ 120); }

		img {
			@include align(cen);
			padding: 0.25rem; } }

	@include media(xs, emp, 0) {
		.item {
			flex: 0 0 percentage(1 / 4);
			max-width: percentage(1 / 4); } }

	@include media(lg, emp, 0) {
		.item {
			flex: 0 0 percentage(1 / 6);
			max-width: percentage(1 / 6); } } }

// FILTER
.filter {
	&-content {
		display: flex;
		flex-wrap: wrap;

		.filter-result {
			flex-grow: 1; }

		.right {
			margin-left: auto; } }

	&-result {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		flex: 0 0 100%;
		max-width: 100%;

		.item {
			margin-right: 1rem;
			margin-bottom: 1rem; }

		.btn-main {
			display: flex;

			&::after {
				content: "x";
				margin-left: 0.5rem; } } }

	&-ctrl {
		flex: 0 0 100%;
		max-width: 100%;
		display: flex;
		align-items: flex-end;
		justify-content: space-between;

		.d-flex {
			flex-wrap: wrap; }

		.btn-main {
			white-space: nowrap; } }

	@include media(md, emp, 0) {
		&-result {
			flex: 0 0 auto; }

		&-ctrl {
			display: block;
			flex: 0 0 auto; } } }

// DETAIL
.detail {
	&-content {
		color: $cl_33;

		.booking-form--verticle {
			padding: 2rem 1.5rem;
			box-shadow: 0 .25rem 1.25rem rgba(0,0,0,0.15); }

		.content {
			font-size: 1rem; }

		&__title {
			.title {
				font-size: 2rem;
				margin-bottom: 0.25rem;
				font-weight: bold; }

			.price {
				font-size: 2rem;
				color: $cl_main;
				font-weight: bold; } }

		&__left {
			// flex: 0 0 calc(100% - 17rem)
			// max-width: calc(100% - 17rem)
			// flex-grow: 1
			// padding-right: 2.5rem
			margin-bottom: 2rem; }

		&__right {
			// flex: 0 0 17rem

			.booking-form--verticle {
				max-width: 25rem;
				margin: 0 auto 1rem; } }

		&__album {
			margin-bottom: 2rem; }

		@include media(md, emp, 0) {
			display: flex;

			&__title {
				display: flex;

				&::after {
					content: "";
					display: block;
					flex: 0 0 17rem;
					max-width: 17rem; }

				.block {
					flex: 0 0 calc(100% - 17rem);
					max-width: calc(100% - 17rem);
					padding-right: 2.5rem; } }

			&__left {
				flex: 0 0 calc(100% - 17rem);
				max-width: calc(100% - 17rem);
				padding-right: 2.5rem;
				margin-bottom: 0; }

			&__right {
				flex: 0 0 17rem;
				max-width: 17rem; }

			&__album {
				margin-bottom: 3rem; } } } }

// CUSTOM JQUERY UI CALENDER
.ui-datepicker {
	padding: 0;
	border: none !important;

	&-header {
		color: $cl_white;
		background-color: $cl_main; }

	&-prev,
	&-next {
		background-color: $cl_white;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		border: none !important;

		&::before {
			font-family: $ficon; }

		span {
			background-image: none !important; }

		&:hover {
			color: $cl_white !important;
			background-color: $cl_yellow; } }

	&-prev {
		&::before {
			content: "\f0d9"; } }

	&-next {
		&::before {
			content: "\f0da"; } } }
