.breadcrumb {
	&k {
		padding-top: 15*$rem;
		padding-bottom: 10*$rem;
		background-color: #f7f7f7;
		margin-bottom: 30*$rem;

		&-list {
			display: flex;
			flex-wrap: wrap;

			.item {
				margin-bottom: .25rem;

				&:not(:last-child) {
					&:after {
						content: "\f105";
						font-family: $ficon;
						margin-left: 1.125rem;
						margin-right: 1.125rem; } } }

			.link {
				color: $cl_main; } } } }
