.slick {
	&-track {
		margin-left: 0;
		margin-right: 0; } }

.slider {
	&-custom {
		margin-top: -1rem;
		margin-bottom: -1rem;

		.slick {
			&-list {
				margin-left: -1rem;
				margin-right: -1rem; }

			&-slide {
				padding: 1rem;

				.item-container {
					margin: 0; }

				&r {
					&:hover {
						.slick-arrow {
							opacity: 1; } } } }

			&-arrow {
				@include align(ver);
				@extend .rs-btn;
				z-index: 1;
				opacity: 0;
				height: 2.5rem;
				width: 1.5rem;
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;
				font-size: 0;
				background-color: $cl_main;
				padding: 0.25rem;

				&::before,
				&::after {
					content: "";
					height: .125rem;
					width: 100%;
					margin-top: -2px;
					background-color: $cl_white;
					transition: transform 350ms; }

				&::before {
					transform: rotate(45deg); }

				&::after {
					transform: rotate(-45deg); }

				&:hover {
					&::before {
						transform: rotate(35deg); }

					&::after {
						transform: rotate(-35deg); } } }

			&-prev {
				left: 0;

				&::before,
				&::after {
					transform-origin: left; } }

			&-next {
				right: 0;

				&::before,
				&::after {
					transform-origin: right; } } }

		&.gutter-md {
			.slick {
				&-list {
					margin-left: -0.75rem;
					margin-right: -0.75rem; }

				&-slide {
					padding-left: 0.75rem;
					padding-right: 0.75rem; } } } }

	&-sync {
		&__for {
			margin-bottom: 1rem;

			.img {
				@include img_box(904, 528); }

			@include media(lg, emp, 0) {
				margin-bottom: 2rem; } }

		&__nav {
			.img {
				@include img_box(1,1); }

			.slick {
				&-track {
					margin-left: auto;
					margin-right: auto; }

				&-list {
					margin-left: -.25rem;
					margin-right: -.25rem; }

				&-slide {
					padding-left: .25rem;
					padding-right: .25rem;

					.img {
						border-radius: 0.375rem;
						border: .125rem solid transparent; } }

				&-current {
					.img {
						border-color: $cl_main; } } }

			@include media(sm, emp, 0) {
				.slick {
					&-list {
						margin-left: -.5rem;
						margin-right: -.5rem; }

					&-slide {
						padding-left: .5rem;
						padding-right: .5rem; } } }

			@include media(lg, emp, 0) {
				.slick {
					&-list {
						margin-left: -1rem;
						margin-right: -1rem; }

					&-slide {
						padding-left: 1rem;
						padding-right: 1rem; } } } } } }
