// TITLE STYLES
.title {
	&-wrap {
		padding-bottom: 1.25rem;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: flex-end;

		.title-block {
			padding-bottom: 0;
			margin-bottom: 0.5rem; }

		.link {
			margin-left: auto;
			margin-bottom: 0.5rem;
			white-space: nowrap; } }

	&-block {
		padding-bottom: 1.75rem;

		~ .slider-custom {
			margin-top: -1rem;
			margin-bottom: -1rem;

			.slick-slide {
				padding-top: 1rem;
				padding-bottom: 1rem; } } }

	//title-hotel-danh gia + binh luan
	&-block-19 {
		padding-bottom: 0.6875rem; }

	&-block-35 {
		padding-bottom: 2.1875rem; }

	&-main {
		font-size: 1.25rem;
		color: $cl_main;
		font-weight: bold;
		margin-bottom: 0; }

	&-description {
		margin-top: 0.25rem;
		margin-bottom: 0;
		font-size: inherit;

		@include media(sm, emp, 0);
		&-main {
			font-size: 1.5rem; } } }
