// RESET CSS
button {
	cursor: pointer;
	transition: 250ms;

	&:focus {
		outline: none; } }

a {
	transition: all 250ms;
	cursor: pointer;
	color: inherit;

	&:hover,
	&:focus {
		text-decoration: none;
		color: inherit; } }

* {
	outline: none; }

img {
	max-width: 100%;
	max-height: 100%; }

textarea {
	resize: none; }

input {
	&[type="number"] {
		-moz-appearance: textfield;

		&::-webkit-outer-spin-button,
		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
			margin: 0; } } }

.rs {
	&-list {
		padding-left: 0;
		margin-bottom: 0;
		list-style: none; }

	&-btn {
		background-color: transparent;
		border: none;
		padding: 0;
		cursor: pointer;
		font-weight: inherit;
		font-size: inherit;
		color: inherit;
		transition: all 250ms;
		display: inline-block; }

	&-scrollbar {
		&::-webkit-scrollbar {
			width: .25rem;
			height: .25rem;
			border-radius: .25rem; }

		&::-webkit-scrollbar-thumb {
			border-radius: .25rem;
			background-color: $cl_main; }

		&::-webkit-scrollbar-track {
			box-shadow: inset 0 0 .25rem 1rem rgba($cl_60, .2); } } }

.form-control:focus {
	box-shadow: none;
	border-color: unset; }
