.selectbox {
	background-color: #fff;
	position: relative;
	height: 34px;
	display: inline-block;
	margin-bottom: 0;
	border: 1px solid #ddd;

	.fa {
		position: absolute;
		right: 10px;
		top: 50%;
		z-index: 1;
		transform: translateY(-50%);
		pointer-events: none; }

	select {
		border: none;
		box-shadow: none;
		background: transparent;
		background-image: none;
		appearance: none;
		padding-left: 10px;
		padding-right: 30px;
		width: 100%;
		height: 100%; } }
