// BUTTON STYLES
.btn {
	&-main {
		color: $cl_white;
		background-color: $cl_main;
		line-height: 1;
		padding: 0.5rem .75rem;
		border-radius: 0.5rem;
		transition: all 250ms;

		.fa {
			margin-left: 0.5rem; }

		&:hover {
			color: $cl_main;
			background-color: $cl_white;
			box-shadow: 0 0 0 1px $cl_main; } }

	&-line {
		font-size: 1.125rem;
		font-weight: bold;
		padding: .5rem 1rem;
		box-shadow: 0 0 0 0.5px $cl_60;

		&:hover,
		&:focus {
			color: $cl_white;
			background-color: $cl_main;
			box-shadow: 0 0 0 0.5px $cl_main; }

		&--main {
			color: $cl_main;

			&:hover,
			&:focus {
				color: $cl_white; } }

		&.bd-white {
			box-shadow: 0 0 0 0.5px $cl_white; } } }

