// RATING STARS
.rating {
	position: relative;
	font-size: 12px;

	.numb {
		// color: $cl_gray2
		font-size: 14px; }

	&-disabled {
		.rating-stars {
			cursor: default !important; } }

	&-container {
		display: inline-block;
		font-size: inherit;

		.empty-stars {
			color: rgba($cl_60, .1); }

		.filled-stars {
			color: $cl_orange;
			text-shadow: none;
			-webkit-text-stroke: inherit; }

		.star {
			font-size: 1em;
			margin: 0 .125rem; } }

	&.without-caption {
		.theme-krajee-uni .star {
			font-size: 20px;
			margin: 0 1px;
			line-height: 1; }

		.rating-container {
			.clear-rating,
			.caption {
				display: none; }


			.filled-stars {
				-webkit-text-stroke: 0;
				text-shadow: none;
				color: $cl_rating_star; } } } }
