// FOOTER
.footer {
	color: $cl_white;
	background-color: $cl_footer_bg;
	padding-top: 2.5rem;
	padding-bottom: 2.5rem;
	margin-top: auto;

	&-logo {
		display: block;
		max-width: 13.75rem;
		margin-bottom: 2rem; }

	&-title {
		padding-top: 1.5rem;
		padding-bottom: 1rem;
		margin-bottom: 0;
		text-transform: uppercase;
		font-weight: bold;
		font-size: inherit; }

	&-hotline {
		font-size: 1.125rem;
		display: flex;
		font-weight: bold;
		margin-bottom: 1rem;

		.icon {
			margin-right: 0.5rem;
			fill: currentColor; } }

	&-list {
		.item {
			&:not(:last-child) {
				margin-bottom: 1rem; } } }

	&-block {
		.social {
			font-size: 1.25rem;

			.item {
				&:not(:last-child) {
					margin-right: 1rem; } } } }

	&-payment {
		display: flex;
		flex-wrap: wrap;

		.item {
			&:not(:last-child) {
				margin-right: 1rem; } } } }
