// ASIDE
.aside {
	&-block {
		&:not(:last-child) {
			margin-bottom: 2.5rem; } }

	&-title {
		font-size: 1.125rem;
		font-weight: bold;
		text-transform: uppercase;
		margin-bottom: 1rem; }

	&-list {
		@extend .rs-scrollbar;
		max-height: 18.75rem;
		overflow-y: auto;

		.item {
			&:not(:last-child) {
				margin-bottom: 0.75rem; } } }

	&-close {
		height: 2.5rem;
		width: 100%;
		position: absolute;
		top: 0;
		left: 0;
		font-size: 1rem;
		color: $cl_white;
		background-color: $cl_main;
		display: flex;
		align-items: center;
		padding: 0.5rem 1.25rem; }

	&.opened {
		transform: rotateY(0); }

	@include media(md, emp, -1) {
		position: fixed;
		top: 0;
		right: 0;
		height: 100vh;
		width: 100%;
		max-width: 20rem;
		background-color: $cl_white;
		z-index: 9;
		padding: 0;
		transform: rotateY(90deg);
		transform-origin: top right;
		transition: all 350ms;

		&::before,
		&::after {
			content: "";
			position: absolute;
			left: 0;
			width: 100%;
			height: 2.5rem;
			z-index: 1;
			pointer-events: none; }

		&::before {
			top: 2.5rem;
			background: linear-gradient(to bottom, $cl_white, rgba(255,255,255,0)); }

		&::after {
			bottom: 0;
			background: linear-gradient(to top, $cl_white, rgba(255,255,255,0)); }

		&-wrap {
			padding: 2.5rem 1.25rem;
			margin-top: 2.5rem;
			overflow-y: auto;
			height: calc(100% - 2.5rem);
			position: relative; } } }
