.hasLink {
	position: relative;

	&>.link {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		opacity: 0;
		z-index: 2; } }

.hasBg {
	.link-bg {
		z-index: 1; }

	@include min(1024px) {
		.link-bg {
			background-color: rgba(#000, .5);
			opacity: 0; }

		&:hover {
			.link-bg {
				opacity: 1; } } } }

.link {
	&:hover {
		color: $cl_main; } }
