// ***** IMAGE BOX
@mixin img_box ($w, $h) {
	&:before {
		padding-top: percentage($h / $w); }

	img {
		@include align(cen);
		@include obfit; } }

//==============================================================================
//==============================================================================

// ***** ARROW
@mixin arrow($dir, $size, $color) {
	height: 0;
	width: 0;
	border-style: solid;
	border-width: $size;
	border-color: transparent;
	display: block;

	@if $dir == "top" {
		border-top: 0;
		border-bottom-color: $color; }
	@else if $dir == "right" {
		border-right: 0;
		border-left-color: $color; }
	@else if $dir == "bottom" {
		border-bottom: 0;
		border-top-color: $color; }
	@else if $dir == "left" {
		border-left: 0;
		border-right-color: $color; } }
