//==============================================================================
//==============================================================================

@mixin clear() {
	&:after {
		content: "";
		display: table;
		clear: both; } }

@mixin align($dir) {
	position: absolute;

	@if $dir == "cen" {
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%); }
	@else if $dir == "ver" {
		top: 50%;
		transform: translateY(-50%); }
	@else if $dir == "hor" {
		left: 50%;
		transform: translateX(-50%); }

	@else if $dir == "hor-left" {
		left: 0;
		top: 50%;
		transform: translateY(-50%); }

	@else if $dir == "hor-right" {
		right: 0;
		top: 50%;
		transform: translateY(-50%); }

	@else if $dir == "ver-top" {
		left: 50%;
		top: 0;
		transform: translateX(-50%); }

	@else if $dir == "ver-bot" {
		left: 50%;
		bottom: 0;
		transform: translateX(-50%); } }

//==============================================================================
//==============================================================================

@mixin nowrap {
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis; }


//==============================================================================
//==============================================================================

@mixin obfit {
	height: 100%;
	width: 100%;
	object-fit: cover;
	object-position: center;
	max-height: none;
	max-width: none; }

//==============================================================================
//==============================================================================

@mixin bgcover {
	background: {
		repeat: no-repeat;
		position: center center;
		size: cover; } }

//==============================================================================
//==============================================================================

@mixin text($fsz, $lh, $r) {
	font-size: $fsz;
	line-height: $lh;
	max-height: #{($fsz * $lh * $r)};
	overflow: hidden; }
