@keyframes fadeUp {
	0% {
		opacity: 0;
		transform: translateY(50px); }
	100% {
		opacity: 1;
		transform: translateY(0); } }

@keyframes fadeDown {
	0% {
		opacity: 0;
		transform: translateY(-50px); }
	100% {
		opacity: 1;
		transform: translateY(0); } }

@keyframes fadeLeft {
	0% {
		opacity: 0;
		transform: translateX(-50px); }
	100% {
		opacity: 1;
		transform: translateY(0); } }

@keyframes fadeMInDown {
	0% {
		opacity: 0;
		margin-top: -30px; }
	100% {
		opacity: 1;
		margin-top: 0; } }

@keyframes dropDown {
	0% {
		opacity: 0;
		transform: translateY(-30px); }
	60% {
		opacity: 1;
		transform: translateY(0); }
	80% {
		opacity: 1;
		transform: translateY(-10px); }
	100% {
		opacity: 1;
		transform: translateY(0); } }

@keyframes dropUp {
	0% {
		opacity: 0;
		transform: scale(0); }
	60% {
		opacity: 1;
		transform: scale(1.15); }
	80% {
		opacity: 1;
		transform: scale(0.9); }
	100% {
		opacity: 1;
		transform: translateY(1); } }
