// HEADER
.header {
	position: sticky;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 5;
	transition: all 250ms;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(#000,.5);
		transition: all 350ms linear 100ms;
		z-index: 5;
		opacity: 0;
		visibility: hidden; }

	.container {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
		padding-top: .5rem;
		padding-bottom: .5rem; }

	&.is-fixed {
		background-color: #fff;
		box-shadow: 0 0 1.5rem rgba(0,0,0, .3);

		// &::before
 }		// 	opacity: 1

	&-logo {
		flex: 0 0 13.75rem;
		max-width: 13.75rem;
		margin-bottom: 0;
		order: 1; }

	&-ctrl {
		display: flex;
		justify-content: space-between;
		color: $cl_main;
		order: 0;
		flex: 0 0 100%;
		max-width: 100%;
		margin-bottom: .5rem;

		.icon {
			fill: currentColor;
			margin-right: 0.25rem; }

		.hotline {
			font-size: 1.125rem; }

		.user {
			position: relative;

			&:hover {
				.user-list {
					display: block; } }

			&-toggle {
				pointer-events: none; }

			&-list {
				position: absolute;
				top: 100%;
				left: 0;
				white-space: nowrap;
				padding-top: .5rem;
				color: $cl_base_text;

				.list {
					background-color: $cl_white;
					box-shadow: 0 .25rem .25rem rgba($cl_main, .15);
					padding: 0.5rem 1rem;
					border-radius: 0.25rem; }

				.item {
					padding-top: 0.25rem;
					padding-bottom: 0.25rem; }

				.link {
					&:hover,
					&:focus {
						color: $cl_main; } } } } }

	@include media(sm, emp, 0) {
		.container {
			padding-top: .875rem;
			padding-bottom: .875rem;
			flex-wrap: nowrap; }

		&-ctrl {
			order: 1;
			justify-content: start;
			margin-bottom: 0;
			margin-left: auto;
			margin-right: 2.5rem;
			flex: 0 0 auto;

			>.item {
				&:not(:last-child) {
						margin-right: 2.5rem; } } } }

	@include media(md, emp, 0) {
		position: fixed; }

	@include media(lg, emp, 0) {
		&-ctrl {
			margin: 0; } } }
