// FONT FACE
@include font-face('Helvetica Neue', '../fonts/HelveticaNeue', normal, normal, ttf otf);

// FONTS
$fr: 'Helvetica Neue';
$fb: inherit;
$fi: inherit;
$fm: inherit;
$fl: inherit;
$ficon: FontAwesome;

.f {
	&r {
		font-family: $fr; }

	&b {
		font-family: $fb; }

	&i {
		font-family: $fi; }

	&l {
		font-family: $fl; }

	&m {
		font-family: $fm; } }

.lh- {
	&40 {
		line-height: 40px; } }

.font- {
	&italic {
		font-style: italic; } }

.text-underline {
	text-decoration: underline; }
