// CHECKBOX
.checkbox {
	user-select: none;
	display: inline-flex;
	align-items: baseline;
	margin-bottom: 0;

	&__icon {
		height: 1rem;
		width: 1rem;
		flex: 0 0 1rem;
		max-width: 1rem;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		font-size: .75rem;
		border: 1px solid $cl_checkbox;
		margin-right: 0.5rem;
		color: $cl_checkbox;
		cursor: pointer;

		&::before {
			content: "\f00c";
			font-family: "FontAwesome";
			opacity: 0;
			transition: opacity 250ms; } }

	&__input {
		display: none;

		&:checked {
			~ .checkbox {
				&__icon {
					border-color: $cl_checkbox !important;

					&::before {
						opacity: 1; } }

				&__label {
					color: $cl_main; } } } }

	&__label {
		flex-grow: 1;
		cursor: pointer;
		transition: all 250ms;

		.rating-container {
			pointer-events: none; } }

	&.radio {
		.checkbox__icon {
			border-radius: 50%;
			position: relative;
			align-items: center;
			justify-content: center;
			border-color: rgba($cl_60, .5);

			&::before {
				content: "";
				height: 70%;
				width: 70%;
				border-radius: 50%;
				background-color: $cl_checkbox; } } } }

// PRICE RANGE
.price-range {
	padding-left: .5rem;
	padding-right: .5rem;

	.ui-slider {
		border: none;
		background-color: rgba($cl_60, .2);
		border-radius: 0.5rem;

		.ui-slider-handle {
			height: 1rem;
			width: 1rem;
			background-color: $cl_main;
			border-radius: 50%;
			border: none;
			cursor: pointer; }

		&-range {
			background-color: $cl_yellow; }

		&-horizontal {
			.ui-slider-handle {
				top: -0.2em; } } }

	&__value {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-left: -0.5rem;
		margin-right: -0.5rem; } }

// FORM CONTROL
.form-control {
	height: 2rem;
	border-radius: 0.5rem;
	font-size: inherit;
	border-color: rgba(96,96,96,.5); }
