.item-comment {
    .show-about {
        &.active {
            display: none; } }
    .bord {
        padding: 2rem 0 1.5rem 0;
        &:first-child {
            padding-top: 0; }
        .content {
            .cont {
                font-size: 1rem;
                position: relative;

                &.active {
                    overflow: hidden;
                    -webkit-line-clamp: 3;
                    display: -webkit-box;
                    -webkit-box-orient: vertical; }
                // &::after
                //     display: inline-block;
                //     content: "[See more...]";
                //     position: absolute;
                //     bottom: 2px;
                //     right: 0px;
                //     background-color: #EFEFEF;
 } }                //     padding: 0 0 0 6px;
        &:not(:last-child) {
            border-bottom: 0.5px solid rgba(96, 96, 96, 0.5); } }
    .author {
        &-img {
            height: 3rem;
            width: 3rem;
            border-radius: 50%;
            overflow: hidden;
            margin-right: 0.5rem;

            img {
                @include obfit; } }
        &-title {
            display: flex;
            flex-wrap: wrap; }
        .name {
            font-size: 1rem;
            font-weight: bold;
            margin-right: 1.5rem;
            margin-bottom: 0.25rem; }

        .cus-rating-2 {
            .rating-container {
                .rating-stars {
                    font-size: 1rem; } } } } }
.cus-rating-1 {
    .rating-container {
        .rating-stars {
            &:first-child {
                font-size: 2rem;
                margin-right: 1.5rem; } } } }

