// COLORS
$cl_green: #35a533;
$cl_white: #fff;
$cl_black: #000;
$cl_red: #c00000;
$cl_brown: #9b9b9b;
$cl_gray: #aaa;
$cl_yellow: #FFE100;

$cl_fa: #fafafa;
$cl_f5: #f5f5f5;
$cl_f023: #f0f2f3;
$cl_e6: #e6e6e6;
$cl_99: #999;
$cl_60: #606060;
$cl_40: #404040;
$cl_33: #333;

$cl_main: #FF8903;
$cl_orange: $cl_yellow;
$cl_title_bg: $cl_f023;
$cl_base_text: #5E5E5E;
$cl_rating_star: $cl_yellow;
$cl_footer_bg: $cl_base_text;
$cl_checkbox: $cl_main;

.cl {
	&-main {
		color: $cl_main; }

	&-white {
		color: $cl_white; }

	&-red {
		color: $cl_red; }

	&-brown {
		color: $cl_brown; }

	&-gray {
		color: $cl_gray; }

	&-orange {
		color: $cl_orange; }

	&-99 {
		color: $cl_99; } }

.text- {
	&red {
		color: #f00; } }

.bg- {
	&e {
		background-color: #eee; }
	&green {
		color: $cl_green; } }
