.banner {
	position: relative;
	background: {
		position: center center;
		size: cover;
		repeat: no-repeat; }

	.booking-form {
		.btn-main {
			width: 100%; } }

	@include media(sm, emp, 0) {
		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 40%;
			background: linear-gradient(180deg, $cl_white 0%, rgba(255, 255, 255, 0) 100%);
			margin-top: -0.25rem;
			pointer-events: none; }

		.booking-form {
			.btn-main {
				width: auto; } } }

	&-index {
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		// padding-top: 3rem
		// padding-bottom: 3rem

		.banner {
			&-title {
				.title {
					font-size: 1.5rem;
					font-weight: bold;
					text-transform: uppercase; } }

			&-combo {
				margin-top: 1.25rem;

				.type {
					text-transform: uppercase;
					margin-bottom: 0.25rem; }

				.name {
					font-size: 1.5rem;
					margin-bottom: 0.25rem;
					font-weight: bold;
					text-transform: uppercase; }

				.price {
					font-size: 1.5rem;
					font-weight: bold; }

				.label {
					font-size: .875rem;
					margin-left: 0.25rem; }

				.description {
					p {
						margin-bottom: 0; } } } }

		.booking-form {
			margin-top: 1.25rem; }

		.container {
			padding-top: 1.75rem;
			padding-bottom: 1.75rem; }

		@include media(sm, emp, 0) {
			height: 100vh;
			min-height: 40rem;
			max-height: 752*$rem;

			.banner {
				&-title {
					.title {
						font-size: 3rem; } }

				&-combo {
					margin-top: 2rem; } }

			.container {
				padding-top: 3rem;
				padding-bottom: 3rem; }

			.booking-form {
				margin-top: 3.5rem; } } }

	&-content {
		position: relative; }

	&-page {
		display: flex;
		align-items: center;
		padding-top: 3.5rem;
		padding-bottom: 3.5rem;

		.banner-content ~ .booking-form {
			margin-top: 2rem; }

		@include media(sm, emp, 0) {
			min-height: 528*$rem;
			padding-top: 5rem; }

		@include media(md, emp, 0) {
			.banner-content ~ .booking-form {
				margin-top: 0; }

			.container {
				display: flex;
				justify-content: space-between; }

			.booking-form {
				max-width: 30rem;
				margin-left: auto; } } } }
