// NAVIGATION
.navigation {
	text-transform: uppercase;
	background-color: $cl_white;
	font-weight: bold;
	order: 1;

	&-open {
		font-size: 1.5rem;
		color: $cl_main;
		order: 1;

		@include media(lg, emp, 0) {
			display: none; } }

	&-link {
		.arrow {
			flex: 0 0 30px;
			max-width: 30px;
			text-align: center;

			.fa {
				pointer-events: none;

				&:before {
					display: inline-block;
					transition: transform 300ms; } }

			&.collapsed {
				.fa:before {
					transform: rotate(-90deg); } } }

		.link {
			white-space: normal;
			flex: 0 0 calc(100% - 30px);
			max-width: calc(100% - 30px); } }

	&-list {
		.item {}

		.link {
			padding-top: 0.25rem;
			padding-bottom: 0.25rem;
			border-bottom: .125rem solid transparent;

			&:hover,
			&:focus,
			&.active {
				color: $cl_main;
				border-color: $cl_main; } } }

	&-close {
		display: flex;
		align-items: center;
		height: 40px;
		width: 100%;
		padding-left: 0.8rem;
		padding-right: 0.8rem;
		color: $cl_white;
		background-color: $cl_main;
		position: absolute;
		top: 0;
		left: 0;

		.fa {
			margin-right: 0.5rem; } }

	.drop-list {
		width: 200px;

		.link {
			font-size: 14*$rem; } }

	@include media(lg, emp, 0) {
		background-color: transparent;

		&-link {
			.link {
				flex: 0 0 100%;
				max-width: 100%;

				&:after {
					content: "\f107";
					font-family: "FontAwesome";
					font-size: 14px;
					margin-left: 5px;
					transition: transform 300ms;
					display: inline-block; } }

			.arrow {
				display: none; } }

		.drop {
			&:hover {
				>.navigation-link {
					.link {
						color: $cl_white;
						background-color: $cl_main;

						&:after {
							transform: rotate(-90deg); } } } } }

		&-list {
			display: flex;
			align-items: center;

			.item {
				&:not(:last-child) {
					margin-right: 4rem; } } }

		&-close {
			display: none; } }

	@include media(lg, emp, -1) {
		position: fixed;
		top: 0;
		right: 0;
		width: 100%;
		max-width: 320px;
		height: 100%;
		transition: 350ms;
		transform: rotateY(90deg);
		transform-origin: top right;
		box-shadow: 0 0 10px rgba($cl_33, .5);
		z-index: 9;

		&.opened {
			transform: rotateY(0); }

		&-wrap {
			height: calc(100% - 40px);
			margin-top: 40px;
			overflow-y: auto; }

		&-list {
			padding: 1.5rem .8rem;

			.item {
				&:not(:last-child) {
					margin-bottom: 1.25rem; } } }

		.drop-list {
			position: static;
			width: 100%;
			box-shadow: none;

			&.child {
				padding-left: 1rem; }

			.item {
				border-bottom: none; } }

		.container {
			height: 100%; } } }
