body {
	font-family: $fr;
	font-size: 0.875rem;
	font-weight: normal;
	color: $cl_base_text;
	// max-width: 1920px
	margin-left: auto;
	margin-right: auto;
	position: relative;
	line-height: 1.3;
	min-height: 100vh;
	display: flex;
	flex-direction: column;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(#000,.5);
		transition: all 350ms linear 100ms;
		z-index: 5;
		opacity: 0;
		visibility: hidden; }

	&.nav-opened {
		overflow: hidden;

		&:before,
		.header::before {
			opacity: 1;
			visibility: visible; } } }

.container {
	@include media(xs, emp, 0) {
		max-width: 90%; }

	@include media(xl, emp, 0) {
		max-width: 1216px; } }

@include media(lg, emp, 0) {
	.row {
			margin-left: -1rem;
			margin-right: -1rem; }

	.container,
	.col,
	[class ^= "col-"] {
		padding-left: 1rem;
		padding-right: 1rem; } }

.nowrap {
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis; }

.back2top {
	cursor: pointer;
	position: fixed;
	transition: all 200ms ease-in-out 200ms;
	z-index: 10;
	font-size: 24px;
	right: 15px;
	bottom: 17px;
	line-height: 0;
	opacity: 0;
	visibility: hidden;
	text-align: center;
	padding-top: 3px;
	background-color: #000;
	color: #fff;
	width: 34px;
	height: 34px;

	&.active {
		opacity: 1;
		visibility: visible;

		&:hover {
			background-color: $cl_orange; } } }
